<template>
	<div class="detail">
		<div class="detail-title">
			<div class="detail-title-text">商业计划书标题商业计划书标题商业计划书标题</div>
			<div class="detail-title-right">
				<div class="detail-title-btn" @click="switchDetailshow()">全屏预览</div>
				<div class="detail-title-btn">使用模板</div>
			</div>
		</div>
		<div class="detail-banner">
			<img :src="require('@/assets/anli/bg1.jpg')" alt="">
		</div>
		<div class="detail-imgs">
			<!-- <div v-for="(item, index) in imgData" :key="index"> -->
				<img :src="require('@/assets/anli/bg1.jpg')" alt="" v-for="(item, index) in imgData" :key="index">
			<!-- </div> -->
		</div>
	</div>
</template>

<script>
import axios from 'axios';
export default {
	name: 'Index',
	data () {
		return {
			imgData: ['', '', '', '']
		}
	},
	methods: {
		// 跳转到全屏展示页面
		switchDetailshow() {
			this.$router.push({name: 'Detailshow', params: {title: '全屏展示页'}})
		}
	},
	mounted () {
		window.onLoad  = () => {
			
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
	.detail {
		padding-top: 64px;
		padding-bottom: 64px;
		&-title {
			width: 80%;
			margin: 20px auto;
			display: flex;
			justify-content: space-between;
			&-right {
				display: flex;
			}
			&-text {
				font-size: 42px;
				color: #5A5A5A;
			}
			&-btn {
				width: 159px;
				height: 53px;
				display: flex;
				align-items: center;
				justify-content: center;
				background-color: #FF4444;
				border-radius: 5px;
				font-size: 24px;
				color: #ffffff;
				cursor: pointer;
			}
			&-btn:nth-child(1) {
				margin-right: 15px;
			}
		}
		&-banner {
			width: 80%;
			margin: auto;
			img {
				width: 100%;
			}
		}
		&-imgs {
			width: 82%;
			margin: auto;
			display: flex;
			flex-wrap: wrap;
			img {
				width: 48%;
				background-color: #ffffff;
				margin: 30px 1% 0;
			}
		}
	}
</style>



